.footer {
	&--docs {
		padding-top: 2.5rem;
	}

	&__helpful-form {
		margin-bottom: 2rem;

		.gform_wrapper ul li.field_description_below div.ginput_container_radio {
			margin-top: 0;
		}

		.gform_wrapper form .gform_body .gform_fields .gfield .ginput_container.ginput_container_radio .gfield_radio {
			display: flex;
		}

		.gform_wrapper form .gform_body .gform_fields .gfield .ginput_container.ginput_container_radio .gfield_radio li input[type=radio] {
			display: none;
		}

		.gform_wrapper ul.gfield_radio li label {
			-webkit-box-align: center;
		    -ms-flex-align: center;
		    align-items: center;
		    display: -webkit-box;
		    display: -ms-flexbox;
		    display: flex;
		    margin: 0 4px;
		    padding: 0.5rem 1rem;
		    border: 1px solid theme-color(lightest-gray);
		    border-radius: 5px;
		    transition: $transition-speed-fast;
		}

		.gform_wrapper ul.gfield_radio {
			margin: 0;
		}

		#field_2_3 {
			align-items: center;
			display: flex;
		}

		.gform_wrapper .top_label .gfield_label {
			color: theme-color(primary);
			margin-bottom: 4px;
			margin-right: 1rem;
		}

		.gform_wrapper ul.gfield_radio li {
			margin: 0;
		}

		#label_2_3_0 {
			border: 1px solid $green;
			color: $green;
			cursor: pointer;

			&:hover {
				background-color: $mint;
		    }

			&::before {
				display: inline-block;
			    font-style: normal;
			    font-variant: normal;
			    text-rendering: auto;
			    -webkit-font-smoothing: antialiased;
			    font-family: "Font Awesome 5 Pro";
			    font-weight: 400;
			    content: "\f164";
			    margin-right: 0.375rem;
			}
		}

		#label_2_3_1 {
			border: 1px solid $blue;
			color: $blue;
			cursor: pointer;

			&:hover {
				background: rgba($blue, 0.15);
		    }

			&::before {
				display: inline-block;
			    font-style: normal;
			    font-variant: normal;
			    text-rendering: auto;
			    -webkit-font-smoothing: antialiased;
			    font-family: "Font Awesome 5 Pro";
			    font-weight: 400;
			    content: "\f165";
			    margin-right: 0.375rem;
			}
		}

		.ginput_container_radio {
			margin-top: 12px;
		}

		.gchoice {
			margin-right: 6px;
		}

		#choice_2_3_0:checked {
			+ #label_2_3_0 {
				background: $green;
				border: 1px solid $green;
				color: $white;
			}
		}

		#choice_2_3_1:checked {
			+ #label_2_3_1 {
				background: $blue;
				border: 1px solid $blue;
				color: $white;
			}
		}

		.form-section-title {
			border-bottom: none !important;
    		padding-bottom: 0 !important;
    		padding-top: 0 !important;

			h3 {
				font-size: 1.125rem;
			}
		}

		.gform_required_legend {
			display: none !important;
		}
	}
}



