.page-block__icon-content-columns {
	padding: 2.5rem 2rem 1.5rem 4.25rem;

	@include media-breakpoint-up(lg) {
		padding: 5rem 3.75rem 2.5rem;
	}

	.row {
		display: flex;
		flex-wrap: wrap;
	}

	.icon-content-column {
		margin-bottom: 3.5rem;

		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
		}
	}

	.icon-content-column__card {
		// border-radius: 0.375rem;
		// box-shadow: 0 2px 8px rgba($black, 0.05);
		display: flex;
		flex-direction: column;
		// height: 100%;
		// justify-content: space-between;
		padding-right: 1.25rem;
		// transition: $transition-speed-fast;

		&:hover {
			// box-shadow: 0 8px 20px rgba($black, 0.1);
			text-decoration: none;

			h2 {
				color: $navy;
			}
			
			p,
			ul {
				color: $charcoal;
				text-decoration: none;
			}
		}

		h2 {
			color: $navy;
		}

		p,
		ul {
			color: $charcoal;
		}

		h2 {
			margin-bottom: 0.75rem;
		}

		h3.eyebrow-text {
			margin-bottom: 0.45rem;
		}

		&__icon {
			margin-bottom: 1.75rem;
		}

		// &__content {
		// 	width: calc(100% - 60px);

		// 	@include media-breakpoint-up(lg) {
		// 		width: calc(100% - 80px);
		// 	}
		// }
	}
}



