.table-of-contents {
	@include media-breakpoint-up(lg) {
		padding-left: 2rem;
		position: sticky;
		top: 115px;
	}

	ul {
		margin: 1rem 0 1.5rem;
		padding: 0;
	}

	li {
		font-size: 0.875rem;
		list-style: none;
		margin-bottom: 0.5rem;
	}	

	a {
		color: darken($pewter, 8);
		display: block;
		line-height: 1.35;
		transition: $transition-speed-fast;

		&:hover {
			color: $green;
			text-decoration: none;
		}
	}
}



